import React from 'react';
import FactCard from '../Components/FactCard';
import TypeWriter from 'react-typewriter';
export default class HomeScreen extends React.Component {
    render(){
        return(
            <div style={{overflowX:"hidden",overflowY:"hidden"}}>
                <div style={{height:"100vh", width:"100vw", backgroundImage:"url('https://a360-rtmagazine.s3.amazonaws.com/wp-content/uploads/2019/05/air-pollution-smog-1500-1280x640.jpg')",  backgroundRepeat: "no-repeat",backgroundSize: "cover"}}>
                    <div className="superCenter" style={{width:"75vw"}}>
                        <p style={{textAlign:"center",fontSize:"5vw",color:"white",fontWeight:'bold'}}>
                            <h1 style={{fontSize:"20vw"}}>POPs</h1>
                            <TypeWriter typing={1} class="display-1" >Persistent Organic Pollutants</TypeWriter>
                        </p>
                    </div>
                </div>
                <div className="container">
                    <h2 className="display-3 text-center">What are POPs?</h2>
                    <p>POPs are blah blah blah....</p>
                    <br/>
                    <h2 className="display-2 text-center">Fun Facts</h2>
                    <p>We encourage you to read this whole website. Use the links in the naviagtion bar at the top of the site to get detailed information. If you don't have time, we have compiled of the list of what we think are the most interesting and most imporant things you need to know.</p>
                    <br/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                    <FactCard text="FACT" src="SOURCE"/>
                </div>
            </div>
        )
    }
}